export default {
	data() {
		return {
			lineItems: [
				{name: '发货',status: '0',active: false},
				{name: '生产',status: '0',active: false},
				{name: '订单下达',status: '1',active: true}
			]
		}
	},
	methods: {
		changeTab(item){
			this.lineItems.forEach(d=>d.active = false)
			item.active = true;
		}
	}
}
